.App {
  text-align: center;
}

* {
  margin: 0; padding: 0; box-sizing: border-box;
}
body {
  background-image: linear-gradient(120deg, #f6d35f 0%, #fda085 100%);
  color: #fff; min-height: 100vh;
}
header {
  font-size: 2rem;
}
header, form {
  min-height: 20vh; display: flex; justify-content: center; align-items: center; 
}
form input, form button {
  padding: 0.5rem; font-size: 2rem; border: 0 none; background-color: #fff;
}
form button {
  color: #ff6f47; background-color: #f7fffe; cursor: pointer;
  transition: all .3s ease;
}
form button:hover {
  background-color: #ff6f47; color: #fff;
}
.todo-container {
  display: flex; justify-content: center; align-items: center;
}
.todo-list {
  min-width: 30%;
}
.todo {
  margin: 0.5rem; background-color: #fff; font-size: 1.5rem; color: #151515;
  display: flex; justify-content: space-between; align-items: center;
  transition: all 1s ease;
}
.filter-todo select {
  padding: 1rem;
}
.todo p {
  flex: 1;
}
.trash-btn, .completed-btn {
  background: #ff6f47; color: #fff; border: 0; padding: 1rem;
  cursor: pointer; font-size: 1rem;
}
.completed-btn {
  background: #0bd4a2;
}
.todo-item {
  padding: 0rem 0.5rem;
}
.completed {
  text-decoration: line-through; opacity: 0.5; font-style: italic;
}

select {
  box-shadow: none; border: 0 !important; background-image: none;
}
select .complete {
  color: #0bd4ae;
}
select {
  color: #ff6f47;
  font-family: 'Poppins', sans-serif; cursor: pointer; width: 12rem;
}
